.weekly-report-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Centrar verticalmente el contenido */
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8); /* Fondo semitransparente */
  border-radius: 10px; /* Bordes redondeados */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra ligera para resaltar */
  max-width: 800px; /* Limitar el ancho máximo */
  width: 100%; /* Ocupar todo el ancho disponible */
  margin: auto; /* Centrar el contenedor tanto horizontal como verticalmente */
  margin-top: 100px; /* Añadir margen superior */
  box-sizing: border-box; /* Asegurar que el padding no cause desbordamiento */
}

.subtitle {
  font-family: "Fascinate Inline", system-ui;
  font-weight: 400;
  font-style: normal;
  font-size: 26px;
  color: #333;
    text-shadow: 2px 2px 4px #6EC5C3;
  margin-bottom: 35px;
  margin-top: 0px;
}

.weekly-report-container div {
  margin-bottom: 20px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  margin-bottom: 10px;
  font-weight: bold;
}

input[type="date"] {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: #007BFF;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  width: 100%;
  max-width: 400px;
}

button:hover {
  background-color: #555;
}
